* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
  color: rgba(255, 255, 255);
  font-size: 100px;
}

.container {
  display: flex;
  flex-wrap: wrap;
}

.block {
  flex-basis: 33.33%;
  flex-shrink: 0;
  height: 300px;
  width: 300px;
  line-height: 300px;
  text-align: center;
}

.block--1 {
  background-color: red;
}

.block--2 {
  background-color: rgb(204, 0, 0);

}

.block--3 {
  background-color: rgb(153, 0, 0);
}

.block--4 {
  background-color: rgb(102, 0, 0);
  order: 6;
}

.block--5 {
  background-color: rgb(51, 0, 0);
  order: 5;
}

.block--6 {
  background-color: black;
  order: 4;
}

@media (max-width: 899px) {
  .block {
    flex-basis: 50%;
  }

  .block--3 {
    order: 4;
  }

  .block--4 {
    order: 3;
  }

  .block--6 {
    order: 6;
  }
}

@media (max-width: 599px) {

  .block {
    flex-basis: 100%;
  }

  .block--3 {
    order: 3;
  }

  .block--4 {
    order: 4;
  }
}
